import React from 'react';
import './App.css';
import ListItems from './ListItems'
import { library } from '@fortawesome/fontawesome-svg-core';
import {faTrash} from '@fortawesome/free-solid-svg-icons';

library.add(faTrash);

class App extends React.Component {
  constructor()
  {
    super();
    this.state = {
      items : [],
      currentItem : {
        key : '',
        text : ''
      }
    }
    this.handleInput = this.handleInput.bind(this);
    this.addItem = this.addItem.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.updateItem = this.updateItem.bind(this);

  }
  handleInput(e){
    this.setState({
      currentItem:{
        text : e.target.value,
        key : Date.now()
      }
    });
    //console.log(this.state.currentItem.text);
  }

  addItem(event){
      event.preventDefault();
      const newItem = this.state.currentItem;
      console.log(newItem);
      if(newItem.text!=='')
      {
          const newItems = [...this.state.items,newItem];
          this.setState({
            items:newItems,
            currentItem : {
              text:'',
              key:''
            }
          });
      }
  }

  deleteItem(key) {
    const filteredItems = this.state.items.filter(item => item.key!==key);
    this.setState({items : filteredItems});
  }

  updateItem(text,key)
  {
    const items = this.state.items;
    items.map(item=>{
      if(item.key===key)
      {
        item.text = text;
      }
    })
    this.setState({items:items})
  }
  render()
  {
    return (
      <div className="App">
        <header>
          <form id="to-do-form" onSubmit={this.addItem}>
            <input type="text" placeholder="Enter the task" value={this.state.currentItem.text} onChange={this.handleInput}></input>
            <button type="submit">Add</button>
          </form>
        </header>
        <ListItems items={this.state.items} deleteItem={this.deleteItem} updateItem={this.updateItem} ></ListItems>
        </div>
        );
  }
}

export default App;
